/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

// ----客製css---- //
// Noto Sans Traditional Chinese
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500&family=Roboto:wght@300;400&display=swap");
$bgc-primary: #aa0601;
$bgc-secondary: #fbf2f2;
$text-light: #ffffff;
$text-dark: #650300;
$text-black: #000;
$line-hight-normal: 1.5;

//layout
$flex: flex;
$row: row;
$flex-1: 1;

//RWD
@mixin phone {
  @media (min-width: 375px) {
    @content;
  }
}

@mixin pad {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin web {
  @media (min-width: 1920px) {
    @content;
  }
}

//Deafult
//共用設定
*,
*::before,
*::after,
body {
  box-sizing: border-box;
  font-family: "Noto Sans TC", sans-serif;
  font-family: "Roboto", sans-serif;
  margin: 0;
  padding: 0;
  color: $text-black;
  line-height: $line-hight-normal;
}

a {
  text-decoration: none;
  color: $text-light;
}

h2 {
  color: $text-dark;
  font-size: 24px;
  font-weight: bold;
  @include pad() {
    font-size: 48px;
  }
}

h3 {
  color: $text-dark;
  font-size: 24px;
  font-weight: bold;
  margin-top: 24px;
  @include pad() {
    font-size: 36px;
  }
}

img {
  max-width: 100%;
  display: block;
}

.bg {
  background-color: $bgc-secondary;
}

.block {
  text-align: center;
}

.wrap {
  max-width: 92%;
  margin: 0 15px;
  padding: 40px 0;
  @include pad() {
    margin: 0 30px;
  }

  @include web() {
    width: 1320px;
    max-width: 90%;
    margin: 0 auto;
    position: relative;
    height: 100%;
    padding: 80px 0;
  }
}

//表頭
.header {
  background-color: $bgc-primary;
  color: $text-light;
  font-size: 20px;
  .logo {
    max-width: 100%;
    width: 80px;
    // display: inline-block;
  }
  .nav {
    display: none;
  }

  @include pad() {
    .headerBox {
      height: 80px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .nav {
        display: flex;
        gap: 64px;
      }
    }
  }
  @include web() {
    .bannerBox {
      display: flex;
      justify-content: end;
    }
  }
}

.header .wrap {
  padding: 16px 0;
}

//表尾
.footer {
  font-weight: 400;
  margin: 0 auto;
  background-color: $bgc-primary;
  color: $text-light;
  .wrap {
    padding: 20px 0;
  }
  .top {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    .nav {
      display: none;
    }
    .social-media {
      display: flex;
      justify-content: end;
      gap: 8px;
      img {
        width: 24px;
      }
    }
    .info {
      font-size: 16px;
      h3 {
        font-weight: 400;
        font-size: 16px;
        color: $text-light;
        margin: 8px;
        .material-symbols-outlined {
          color: $text-light;
          font-size: 16px;
          vertical-align: middle;
          margin-right: 11px;
        }
      }
    }
  }
  .copyright {
    margin: 24px 0 0 0;
    padding: 8px 0 0 0;
    border-top: 1px solid $text-light;
    color: $text-light;
    li {
      color: $text-light;
      padding-top: 8px;
    }
    // display: flex;
    // flex-direction: row;
    // justify-content: space-between;
    // align-items: center;
  }

  @include pad() {
    .wrap {
      padding: 40px 0;
      .top {
        flex-direction: column;
        .content {
          padding-top: 16px;
          display: $flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 24px;
          .nav {
            display: flex;
            gap: 48px;
          }
          .social-media {
            gap: 16px;
            img {
              width: 40px;
            }
          }
        }
        .info {
          h3 {
            font-size: 36px;
            .material-symbols-outlined {
              font-size: 36px;
              margin-right: 22px;
            }
          }
        }
      }
      .copyright {
        margin: 32px 0 0 0;
        padding: 32px 0 0 0;
        border-top: 1px solid $text-light;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        ul:nth-child(2) {
          display: flex;
          gap: 48px;
        }
      }
    }
  }
}

// 首頁 --start
.banner {
  height: 396px;
  background-image: url(../images/home-header-sm.png);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: end;
  align-items: center;
  // z-index: -1;
  .bannerBox {
    display: flex;
    justify-content: end;
    align-items: center;
    color: $text-dark;
    .title {
      text-align: right;
      font-weight: 500;
      p {
        font-size: 16px;
      }
      h1 {
        font-size: 24px;
        font-weight: bold;
      }
      span {
        background-color: #aa0601;
        color: $text-light;
        // border: 1px solid #650300;
        font-size: 20px;
        font-weight: 600;
        padding: 6px 12px;
      }
      @include pad() {
        p {
          font-size: 36px;
        }
        h1 {
          font-size: 64px;
          font-weight: 900;
        }
      }
    }
  }
  @include pad() {
    height: 787px;
  }
  @include web() {
    background-image: url(../images/home-header.png);
  }
}

.introduceBox {
  text-align: center;
  // .row{
  //   display: unset;
  // }
  h3 {
    margin-top: 16px;
    color: $text-dark;
  }
  p {
    color: $text-black;
    margin-top: 8px;
  }
  img {
    width: 84px;
    margin-top: 24px;
    display: inline;
  }
  @include pad() {
    .row {
      display: $flex;
      gap: 24px;
      .item {
        flex: $flex-1;
      }
    }
  }
  @include web() {
    display: flex;
    gap: 24px;
    justify-content: center;
    .item {
      margin-top: 33px;
      flex: 1;
      text-align: center;
      h3 {
        margin-top: 24px;
        font-size: 24px;
        font-weight: bold;
      }
      p {
        margin-top: 8px;
        font-size: 16px;
      }
    }
  }
}

.classicBox {
  .item {
    img {
      display: inline-block;
    }
    text-align: center;
  }

  h3 {
    margin-top: 8px;
    font-size: 48px;
    font-weight: 900;
    font-style: italic;
    color: #aa0601;
  }
  @include pad() {
    img {
      margin-top: 48px;
      width: 336px;
    }
  }
  @include web() {
    display: flex;
    justify-content: center;
    gap: 24px;
    // flex-wrap: nowrap;
  }
}

.co-branding {
  background-image: url(../images/index-bg-sm.png);
  background-size: cover;
  background-position: center;
  h2 {
    color: $text-light;
  }
  h3 {
    margin: 0;
    font-size: 48px;
    font-weight: 900;
    font-style: italic;
    color: #aa0601;
    background-color: #fff;
    padding: 8px 0;
  }
  .item {
    width: 100%;
    margin-top: 24px;
    .img_1 {
      background-image: url(../images/home-section4-1-sm.png);
      height: 240px;
      width: 100%;
      background-size: cover;
    }
    .img_2 {
      background-image: url(../images/home-section4-2-sm.png);
      height: 240px;
      width: 100%;
      background-size: cover;
    }
  }
  @include pad() {
    .item {
      margin-top: 48px;
      .img_1 {
        background-image: url(../images/home-section4-1.png);
      }
      .img_2 {
        background-image: url(../images/home-section4-2.png);
      }
    }
  }
  @include web() {
    .cbBox {
      display: flex;
      .img_1 {
        background-image: url(../images/home-section4-1.png);
      }
      .img_2 {
        background-image: url(../images/home-section4-2.png);
      }
    }
  }
}

.recommend {
  h2 {
    color: $text-dark;
  }
  //

  .item {
    text-align: left;
    border-radius: 4px;
    margin-top: 24px;
    box-shadow: 0px 2px 6px #00000029;
    .img_1 {
      border-radius: 4px 4px 0px 0px;
      height: 306px;
      background-image: url(../images/home-section5-1.png);
      background-size: cover;
      background-position: center;
    }
    .img_2 {
      border-radius: 4px 4px 0px 0px;
      height: 306px;
      background-image: url(../images/home-section5-2.png);
      background-size: cover;
      background-position: center;
    }
    .img_3 {
      border-radius: 4px 4px 0px 0px;
      height: 306px;
      background-image: url(../images/home-section5-3.png);
      background-size: cover;
      background-position: center;
    }
    .img_4 {
      border-radius: 4px 4px 0px 0px;
      height: 306px;
      background-image: url(../images/home-section5-4.png);
      background-size: cover;
      background-position: center;
    }
    .card {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 16px;
      height: 205px;
      .content {
        h3 {
          margin: 0;
          font-weight: bold;
          font-size: 16px;
          color: $text-black;
          margin-bottom: 8px;
        }
        p {
          color: $text-black;
        }
      }
      .date {
        color: #707070;
        font-size: 14px;
        font-weight: 400;
      }
      @include pad() {
        gap: 24px;
        .card {
          margin-top: 48px;
          display: flex;
          h3 {
            font-weight: bold;
            margin-bottom: 8px;
          }
        }
      }
    }
  }
  @include pad() {
    .row {
      display: $flex;
      gap: 24px;
      .item {
        flex: $flex-1;
      }
    }
  }
  @include web() {
    display: flex;
    gap: 24px;
  }
}

.contact {
  // h2{

  // }
  .wrap {
    text-align: left;
    max-width: 100%;
    font-size: 16px;

    h2 {
      text-align: center;
      color: $text-dark;
    }
    .subscrube {
      font-size: 16px;
      margin: 24px 0;
    }
    p,
    span {
      margin: 16px 0 0 0;
      color: $text-black;
    }
    input,
    textarea {
      font-size: 16px;
      margin-top: 8px;
      width: 100%;
      height: 36px;
      background-color: transparent;
      padding: 6px 12px;
      border: 1px solid $text-black;
      &::placeholder {
        color: $text-black;
        opacity: 0.2;
      }
      &[type="checkbox"] {
        margin: 0 6px 0 0;
        vertical-align: middle;
        border-radius: 0%;
        width: 16px;
        user-select: none; /* 防止文字被滑鼠選取反白 */
      }
    }
    textarea {
      height: 136px;
      resize: none;
    }
    span {
      vertical-align: middle;
      margin: 0;
      padding: 0;
    }
    .agree {
      span {
        margin: 0;
      }
      margin: 16px 0 0 0;
    }
    .submit {
      margin-top: 24px;
      display: flex;
      justify-content: center;
      div {
        text-align: center;
        width: 416px;
        padding: 16px 0;
        line-height: 1.5;
        background-color: $bgc-primary;
        color: $bgc-secondary;
      }
    }
  }
  @include pad() {
    .wrap {
      width: 576px;
      margin: 0 auto;
      .subscrube {
        font-size: 20px;
        margin: 16px 0 8px 0;
      }
      p,
      span {
        margin: 24px 0 0 0;
        color: $text-black;
      }
      .submit {
        margin-top: 48px;
      }
    }
  }
  @include web() {
    .wrap {
      width: 636px;
      .subscrube {
        margin: 48px 0 24px 0;
      }
    }
  }
}
// 首頁 --end

// 內頁 --start
.store {
  h2 {
    border-left: 8px solid $bgc-primary;
    padding-left: 12px;
  }
  .selectBox {
    display: flex;
    gap: 8px;
    align-items: center;
    padding: 31px 0;
    .select {
      width: 273px;
      padding: 5px 12px;
      font-size: 18px;
      color: #495057;
      border: 1px solid #ced4da;
      position: relative;
      &:after {
        position: absolute;
        top: 45%;
        right: 12px;
        content: "";
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 5px 5px 0 5px;
        border-color: #000000 transparent transparent transparent;
      }
    }
    @include pad() {
      gap: 16px;
      p {
        font-size: 24px;
      }
    }
  }
  .content {
    .shop {
      .img {
        flex: 1;
        height: 224px;
        background-image: url(../images/store-1.png);
        background-size: cover;
        background-position: center;
      }
      .title {
        font-size: 24px;
        border: 1px solid #00000020;
        padding: 8px 24px;
      }
      ul {
        font-size: 20px;
        padding: 16px;
        border: 1px solid #00000020;
        .material-symbols-outlined {
          margin-right: 11px;
          font-size: 20px;
          vertical-align: middle;
        }
      }

      @include pad() {
        display: flex;
        .img {
          flex: 1;
        }
        .table {
          flex: 1;
          .title {
            font-size: 36px;
          }
          ul {
            margin-top: -1px;
            padding: 16px 27px;
          }
        }
      }
      @include web() {
        display: block;
      }
    }
    .map {
      margin-top: 24px;
      .img {
        height: 414px;
        background-image: url(../images/map.png);
        background-size: cover;
        background-position: center;
        margin-bottom: 24px;
      }
    }
    @include pad() {
      .wrap {
        padding: 80px 0;
      }
    }
    @include web() {
      display: flex;
      gap: 24px;
      .shop{
        flex: 1;
      }
      .map{
        width: 746px;
        // flex: 1;
        margin: 0;
        img{
          
          height: 414px;
        }
      }
    }
  }
}
